
  async function ready() {
    let params = new URLSearchParams(document.location.search);
    let edition = params.get("edition");
    let bareedition = params.get("bareedition") || edition.replace(/\d+/g, '').split('-')[1].trim()
    let num = params.get("num");
    if (!bareedition || !num)
      return
    let editionsJSON = await getJSON('editions')


    let isocodes = await getJSON('isocodes/iso-codes', quranLinks)
    let edtionsLangArr = editionsJSON[bareedition].collection.map(e => [e.name, e.language, e.direction])
    let linksArr = []
    // Table of content
    let tableElem = getElementFromHTML(tableContainer).querySelector('.table')
    for (let [editionName, lang] of edtionsLangArr) {
      let aElem = getElement('a', { href: `#${lang.toLowerCase()}` })
      aElem.innerText = lang
      let tr = getElement('tr')
      let td = getElement('td',{class:'text-center'})
      td.appendChild(aElem)
      tr.appendChild(td)
      tableElem.querySelector('tbody').appendChild(tr)
      // Create endpoints for parallel fetch
      linksArr.push(`editions/${editionName}/${num}`)
    }
    document.querySelector('#mycontainer').appendChild(tableElem)
    let dataArr = await getJSON(linksArr)
    let count = 0
    for (let [editionName, lang, dirval] of edtionsLangArr) {
      let data = dataArr[count]
      let h2 = getElement('h2', { id: lang.toLowerCase(),'class':'text-center' })
      let aElem = getElement('a',{href:`#${lang.toLowerCase()}`})
      aElem.innerText = lang
      h2.appendChild(aElem)
      document.querySelector('#mycontainer').appendChild(h2)
      for (let hadith of data.hadiths) 
      document.querySelector('#mycontainer').appendChild(getHadithCardElem(hadith,dirval,lang,isocodes))
      
      count++;
    }



  }

  document.addEventListener("DOMContentLoaded", ready);

let htmlHadithContainer = 
`    
<div class="card text-dark bg-light m-5">
<div class="card-body">
<div class="card-text lead m-1"></div>
</div>

<span id="footercontainer">
</span>

</div>
`

let tableContainer = 
`
<table class="table table-hover  table-striped">
  <tbody>

  </tbody>
</table>
`

function capitalize(words){
return words.toString().toLowerCase().replace(/(^\w{1})|(\s+\w{1})/g, match => match.toUpperCase()).trim()
}

let htmlparser = new DOMParser();

function getElement(elementName, attributesObj) {
  if(!attributesObj)
    attributesObj = {}
  let element = document.createElement(elementName);
  for (let [key, value] of Object.entries(attributesObj)) {
    element.setAttribute(key, value);
  }
  return element
}

let apiLinks = ["https://cdn.jsdelivr.net/gh/fawazahmed0/hadith-api@1/", "https://gitcdn.link/cdn/fawazahmed0/hadith-api/1/", "https://raw.githubusercontent.com/fawazahmed0/hadith-api/1/"]
let quranLinks = ["https://cdn.jsdelivr.net/gh/fawazahmed0/quran-api@1/", "https://gitcdn.link/cdn/fawazahmed0/quran-api/1/", "https://raw.githubusercontent.com/fawazahmed0/quran-api/1/"]
let extensions = [".min.json", ".json"]

// https://www.shawntabrizi.com/code/programmatically-fetch-multiple-apis-parallel-using-async-await-javascript/
// Get links async i.e in parallel
async function getJSON(endpoints, links) {
  if (!Array.isArray(endpoints))
    endpoints = [endpoints]
  let result = await Promise.all(
    endpoints.map(endpoint => fetchWithFallback(getURLs(endpoint, links)).then(response => response.json()))
  ).catch(console.error)
  if (result.length == 1)
    return result[0]
  return result
}


async function fetchWithFallback(links, obj) {
  let response;
  for (let link of links) {
    try {
      response = await fetch(link, obj)
      if (response.ok)
        return response
    } catch (e) { }
  }
  return response
}

// convert endpoint into multiple urls, including fallback urls
function getURLs(endpoint, links) {
  links = links || apiLinks
  return extensions.map(ext => links.map(e => e + endpoint + ext)).flat()
}

function getElementFromHTML(htmlString){
  return htmlparser.parseFromString(htmlString, "text/html");
}

// pass hadith object & get card element with all hadith info in it
function getHadithCardElem(hadith,dirval,lang,isocodes){
  let lowerLang = lang.toLowerCase()
  let cardElem = getElementFromHTML(htmlHadithContainer).querySelector('.card')
  cardElem.querySelector('.card-text').innerText = hadith.text
  let footerDiv = getElement('div',{class:"card-footer"})
  if(hadith.grades.length>0){
    cardElem.querySelector('#footercontainer').appendChild(footerDiv.cloneNode())
  Array.from(cardElem.querySelectorAll('.card-footer')).at(-1).insertAdjacentHTML("beforeend", `<b>Grades:</b><br>`);
  }
  
  for (let grade of hadith.grades) 
    cardElem.querySelector('.card-footer').insertAdjacentHTML("beforeend", `<b>${capitalize(grade.grade)}</b> : ${grade.name}<br>`);
    if(hadith.hadithnumber){
      cardElem.querySelector('#footercontainer').appendChild(footerDiv.cloneNode())
    Array.from(cardElem.querySelectorAll('.card-footer')).at(-1).insertAdjacentHTML("beforeend", `<a href=#hadith${hadith.hadithnumber} class="link-dark text-decoration-none" >Hadith Number: ${hadith.hadithnumber}</a><br>`);
    }
    if(hadith.arabicnumber){
      cardElem.querySelector('#footercontainer').appendChild(footerDiv.cloneNode())
    Array.from(cardElem.querySelectorAll('.card-footer')).at(-1).insertAdjacentHTML("beforeend", `<a href=#hadith${hadith.hadithnumber} class="link-dark text-decoration-none" >Arabic Number: ${hadith.arabicnumber}</a><br>`);
    }

    if(hadith.reference){
      cardElem.querySelector('#footercontainer').appendChild(footerDiv.cloneNode())
    Array.from(cardElem.querySelectorAll('.card-footer')).at(-1).insertAdjacentHTML("beforeend", `<a href=#hadith${hadith.hadithnumber} class="link-dark text-decoration-none" >Reference: ${Object.entries(hadith.reference).flat().map(e=>capitalize(e)).join(' ')}</a><br>`);
    }
    cardElem.setAttribute('id','hadith'+hadith.hadithnumber)

    cardElem.querySelector('.card-text').setAttribute('dir',dirval)
    cardElem.querySelector('.card-text').setAttribute('lang',isocodes[lowerLang].iso1 ? isocodes[lowerLang].iso1 : isocodes[lowerLang].iso2)

return cardElem
}